<script>
import { createEventDispatcher, onMount } from 'svelte';
import { isPreviewable } from './Utils.svelte';
import SError from './ServerError.svelte';
export let isMobile;
export let msg;
export let query;
export let safe;
export let resp;
export let page;
let limits = [15, 32, 60, 10];
$: isLoading = newResults(resp);
// $: firstPagninationNo = page > 6 ? page - 6 + 1: 1;

const dispatch = createEventDispatcher();
function processing(newPageNo, toRedirect) {
  dispatch('searching', {
    query: null,
    page: newPageNo,
    redirect: toRedirect
  });
  if (isMobile === false) {
    window.scrollTo(0, 0);
  }
}

function getRandLength(min, round) {
  var val = min + Math.floor(Math.random() * round) + 1;
  if (val >= 100) {
    return 100;
  } else {
    return val;
  }
}

function setPageBounds(isMobile) {
  if (isMobile === true) {
    limits = [85, 85, 95, 25];
    isMobile = true;
  } else {
    limits = [50, 68, 95, 12];
    isMobile = false;
  }
}

// Event listener used to check if the preview feature can be enabled
$: pwidth = 0;
function addPreviewSizeEventListener() {
  var width = window.innerWidth;
  if (width >= 1500) {
    if (width >= 1725) {
      pwidth = 600;
    } else {
      pwidth = 460;
    }
  } else {
    pwidth = 0;
  }
}

$: purl = null;
function getMobileUrlIfAny(url) {
  var tmp = url.match(/^https:\/\/..\.wikipedia.org\/([-a-zA-Z0-9()@:%_\\+.~#?&//=]*)/g);
  if (tmp && url === tmp[0]) {
    var url = new URL(url);
    url.hostname=url.hostname.substring(0,2) + '.m' + url.hostname.substring(2, url.hostname.length);
    return url.href;
  } else {
    return url;
  }
}
function setPreview(url) {
  purl=getMobileUrlIfAny(url);
}

// Variable and function used for infinite scrolling
let resultsNo = 0;
function newResults(resp) {
  if (resp && resp.results) {
    // If wiki is found, immediately display it as the preview
    purl=null;
    if (!isNaN(resp.results.length)) {
      for (var i = 0; i < resp.results.length; i++) {
        if (isPreviewable(resp.results[i].href)) {
          purl=getMobileUrlIfAny(resp.results[i].href);
          break;
        }
      } 
    }
    if (page == 1 && resultsNo == 0) {
      // If its first page
      resultsNo = resp.results.length;
      return false;
    } else if (resultsNo !== resp.results.length) {
      // If subsequent pages
        resultsNo = resp.results.length;
        var loader = document.getElementById('infiniteLoader');
        if (loader) {
          loader.style.visibility = 'hidden';
        }
        return false;
    } else {
      return true;
    }
  }
  return false;
}

onMount(() => {
  document.getElementById('searchBarT').value = decodeURIComponent(query).replace(/\+/g, ' ');
  setPageBounds(isMobile);
  window.addEventListener('scroll', () => {
    if (isLoading === false && document.documentElement.scrollTop + document.documentElement.clientHeight >= document.documentElement.scrollHeight) {
      var loader = document.getElementById('infiniteLoader');
      if (loader) {
        loader.style.visibility = 'visible';
        isLoading = true;
        processing(parseInt(page) + 1, false);
        window.setTimeout(() => {
          // TO CHECK WHEN GO LIVE - cos settimeout doesnt seem to work in local. Only will work when build for prod.
          var loader = document.getElementById('infiniteLoader');
          if (loader) {
            loader.style.visibility = 'hidden';
            isLoading = false;
          }
        }, 8000);
      }
    }
  });
  window.addEventListener('resize', addPreviewSizeEventListener);
  addPreviewSizeEventListener();
});
</script>

{#if resp == 'SEARCH_ERROR'}
  <svelte:component this={SError} />
{:else}
  <input type="hidden" id="safeVal" name="safeVal" value="{safe}">
  <div id="wpage" class="flex content-around flex-wrap">
  <div class="relative mt-40 md:mt-20 md:pt-8 items-center w-full md:w-4/5 lg:w-3/5 max-w-6xl">
    <!-- Results -->
    {#if resp && resp.results && resp.results.length > 1}
      <style type="text/css">
          a {text-decoration: none;}
          a:hover {text-decoration: underline;}
      </style>
      {#each resp.results as result}
        <div class="card py-3 md:py-4 px-4 md:px-20 lg:px-20">
          <div class="flex content-around">
            <p class="res-href text-xs self-center pr-5">{result.href}</p>
            {#if pwidth > 0 && isPreviewable(result.href)}
              <button on:click={() => setPreview(result.href)} class="bg-orange-700 hover:bg-orange-900 text-xs text-white font-bold px-3 rounded-full">{msg.app.websearch.btnPreview}</button>
            {/if}
          </div>
          <a class="res-title text-base md:text-xl" href="{result.href}" referrerpolicy="no-referrer">{result.title}</a>
          <p class="res-desc text-sm">{result.desc}</p>
        </div>
      {/each}
    {:else}
      {#each Array(10) as _, i}
        <div class="card py-4 px-4 md:px-20 lg:px-20">
          <span class="skeleton-box h-5 inline-block" style="width: {getRandLength(limits[0], 10)}%;"></span><br>
          <span class="skeleton-box h-10 inline-block" style="width: {getRandLength(limits[1], 10)}%;"></span><br>
          <span class="skeleton-box inline-block" style="width: {getRandLength(limits[2], 10)}%; height: {getRandLength(limits[3], 100)}px;"></span><br>
        </div>
      {/each}
    {/if}

    {#if resp && resp.results && resp.results.length > 1}
      {#if isMobile}
        <!-- Infinite scroll - mobile -->
        <div id="infiniteLoader" class="flex justify-center pb-6" style="visibility: hidden;">
          <svg style="width:100px; height:100px; border-radius:50%; animation:Rotate 2s linear infinite;">
            <circle cx="50" cy="50" r="20"/>
          </svg>
        </div>
        <style>
          @keyframes Rotate{
            0%{
              transform:rotate(0deg);
            }
            100%{
              transform:rotate(360deg);
            }
          }
          svg circle{
            width:100%;
            height:100%;
            fill:none;
            stroke-width:10px;
            stroke:var(--infiniteS-loading);
            stroke-linecap:round;
            stroke-dasharray:300;
            stroke-dashoffset:300;
            animation:Loading 2s linear infinite;
          }
          @keyframes Loading{
            0%,100%{
              stroke-dashoffset:300;
            }
            50%{
              stroke-dashoffset:0;
            }
            50.1%{
              stroke-dashoffset:600;
            }
          }
        </style>
      {:else if !isMobile}
        <!-- Page navigation - desktop -->
        <br><br>
        <div class="flex justify-between pb-16 md:px-20">
          {#if page == 1}
            <button disabled style="visibility: hidden;" class="pagii font-bold py-2 px-12 text-lg">&laquo;&nbsp;&nbsp;&nbsp; {msg.app.pagination.prev}</button>
          {:else}
            <button class="pagii font-bold py-2 px-12 text-lg" on:click={() => processing(page - 1, true)}>&laquo;&nbsp;&nbsp;&nbsp; {msg.app.pagination.prev}</button>
          {/if}

          {#if page == 5}
            <button disabled style="visibility: hidden;" class="pagii font-bold py-2 px-12 text-lg">{msg.app.pagination.next} &nbsp;&nbsp;&nbsp;&raquo;</button>
          {:else}
            <button class="pagii font-bold py-2 px-12 text-lg" on:click={() => processing(page + 1, true)}>{msg.app.pagination.next} &nbsp;&nbsp;&nbsp;&raquo;</button>
          {/if}
        </div>
      {/if}
    {/if}
  </div>

  <!-- Preview -->
  {#if purl && pwidth > 0 && resp && resp.results && resp.results.length > 1}
    <div class="relative mt-32 mr-4 lg:mr-0 ml-0 xl:ml-16 items-center wrap p-5 h-auto" style="height: 650px;">
      <div class="flex content-around pb-3 justify-between">
        <div class="flex content-around pb-3">
          <p class="sub-header text-xl font-semibold tooltip">{msg.app.websearch.titlePreview} <span class="tooltiptext text-base">{msg.app.websearch.toolTip}</span></p>
          <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-info-circle" width="16" height="16" viewBox="0 0 24 24" stroke-width="1.5" stroke="#F44336" fill="none" stroke-linecap="round" stroke-linejoin="round">
            <path stroke="none" d="M0 0h24v24H0z"/>
            <circle cx="12" cy="12" r="9" />
            <line x1="12" y1="8" x2="12.01" y2="8" />
            <polyline points="11 12 12 12 12 16 13 16" />
          </svg>
        </div>
        <button on:click={() => purl=null}>
          <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-x self-center" width="24" height="24" viewBox="0 0 24 24" stroke-width="3" stroke="var(--results-href)" fill="none" stroke-linecap="round" stroke-linejoin="round">
            <path stroke="none" d="M0 0h24v24H0z"/>
            <line x1="18" y1="6" x2="6" y2="18" />
            <line x1="6" y1="6" x2="18" y2="18" />
          </svg>
        </button>
      </div>
      <iframe id="frame" src="{purl}" title="Preview" sandbox referrerpolicy="no-referrer" width="{pwidth}" class="h-full pb-20"></iframe> 
    </div>
  {/if}
  </div>
{/if}

<style>
.wrap {
  overflow: hidden;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  overflow-x:hidden;
  overflow-Y:hidden;
  outline-style: solid;
  outline-width: thin;
  color: var(--pagi-bg-inactive);
  height: 1000px;
}
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}
.tooltip .tooltiptext {
  visibility: hidden;
  width: 300px;
  background-color: var(--tooltip-bg);
  color: var(--gen-text);
  text-align: center;
  border-radius: 6px;
  padding: 10px 10px;
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 130%;
}
.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 20%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent var(--tooltip-bg) transparent transparent;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}
</style>