<script>
import { createEventDispatcher, onMount } from 'svelte';
export let msg;

const dispatch = createEventDispatcher();
function processing(e) {
  if (e.key === "Enter") {
    e.preventDefault();
    search();
  }
}

function search() {
  if (document.getElementById("searchBar").value) {
    dispatch('searching', {
      query: document.getElementById("searchBar").value,
      page: 1,
      redirect: true
    });
  }
}

onMount(() => {
  var origin = window.location.origin + '/';
  if (window.location.href !== origin) {
    window.history.pushState({path: '/'}, '', origin);
  }
  document.getElementById('searchBar').focus();
});
</script>

<div class="pt-24 items-center">
  <!-- Title -->
  <h1 class="page-header flex justify-center text-7xl md:text-8xl lg:text-10xl">{msg.title.complete}</h1>
  <br><br>

  <!-- Search -->
  <div class="pt-5">
    <ul class="justify-between flex-1">
      <li class="flex w-full justify-center mt-1">
        <span class="flex w-full justify-center">
          <div class="relative search-icon mt-2 md:mt-4 w-6 object-center">
            <svg class="fill-current pointer-events-none w-5 h-6 md:w-6 md:h-6 md:ml-3" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
              <path d="M12.9 14.32a8 8 0 1 1 1.41-1.41l5.35 5.33-1.42 1.42-5.33-5.34zM8 14A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"></path>
            </svg>
          </div>
          <input type="search" placeholder="{msg.searchPholder}" id="searchBar" class="bg-search-bar shadow-xl w-screen md:w-1/2 md:justify-center text-xl md:text-3xl transition border outline-none
              rounded-lg py-1 px-2 pl-10 md:pl-12 lg:pl-16 mr-4 appearance-none leading-normal" maxlength="100" on:keydown={processing}>
        </span>
      </li>
    </ul>
  </div>

  <br><br>
  <div class="flex justify-center">
    <button class="search-btn text-xl font-semibold py-2 px-4 rounded shadow-2xl" on:click={search}>{msg.searchBtn}</button>
  </div>
</div>

<style>
.bg-search-bar:focus {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.123);
}
</style>